import React from "react";
import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { filterCreated, filterTemp, getDogs, getTemp, orderByName, orderByWeight, searchDog } from "../../actions";
import { Link } from "react-router-dom";
import { Card } from '../Card/card.jsx'
import { Paginado } from "../Paginado/Paginado";
import { SearchBar } from "../Buscador/Buscador.jsx";
import { ResultSearch } from "../Buscador/ResultSearch";
// import { NavBar } from "../NavBar/NavBar";


export default function Home() {
    const dispatch = useDispatch();

    const allDogs = useSelector((state) => state.dogs);
    const allTemp = useSelector((state) => state.temp);
    const [orden, setOrden] = useState(``);
    const [dogName,setDogName] = useState('')
    

    const [currentPage, setCurrentPage] = useState(1);
    const [dogPerPage, setDogPerPage] = useState(8);
    const indexLastDog = currentPage * dogPerPage;
    const indexFirstDog = indexLastDog - dogPerPage;
    const currentDogs = allDogs.slice(indexFirstDog, indexLastDog);

    const paginado = (pageNumber) => {
        setCurrentPage(pageNumber)
    };

    useEffect(()=> {
        dispatch(getTemp());      
    },[dispatch])

    useEffect(() => {
        dispatch(getDogs());
    }, [dispatch])
//click
    function handleClick(e) {
        e.preventDefault();
        dispatch(getDogs());
    }
//filter
    function handleFilterCreate(e){
        dispatch(filterCreated(e.target.value))
    }

    function handleFilterTemp(e){
        dispatch(filterTemp(e.target.value))
    }
//order
    function handleOrderByName(e){
        e.preventDefault();
        dispatch(orderByName(e.target.value));
        setCurrentPage(1);
        setOrden(`Ordenado ${e.target.value}`);            

    }
    function handleOrderByWeight(e){
        e.preventDefault();
        dispatch(orderByWeight(e.target.value));
        setCurrentPage(1);
        setOrden(`Ordenado ${e.target.value}`) 
    }

    //search

    function handleSearch(e){
        e.preventDefault();
        dispatch(setDogName(e.target.value))
    }

    function handleSubmit(e){
        e.preventDefault();
        dispatch(searchDog(dogName))
    }

    return (
        <div>
            <h1>DOGS</h1>            
            <div>
            <label htmlFor="buscar_perro">Buscar Perrito</label>
            <input onChange={e => { handleSearch(e) }} type="text" id="buscar_perro" value={dogName}></input>
            <button onClick={e=>{ handleSubmit(e) }}>Enviar</button>
        </div>
                    <ResultSearch />
            <Link to='/dog'>
                <button>Crear Perritos</button>
            </Link>
            <button onClick={e => { handleClick(e) }}>Recargar los Dog</button>
            <div>
            <select onChange={e => {handleOrderByName(e) }}>
                <option>seleccione una opcion</option>
                    <option value='asc'>Ordenar por Nombre de la A a la Z</option>
                    <option value='desc'>Ordenar por Nombre de la Z a la A</option>
                </select>
                <select onChange={e=>{handleOrderByWeight(e) }}>
                
                    <option value='weight_max'>Ordenar por Peso mayor</option>                    
                    <option value='weight_min'>Ordenar por Peso menor </option>                    
                </select>
                <select onChange={e =>{handleFilterTemp(e)}}>
                    <option value='All'>Todos los Perros</option>
                    {allTemp.map(c=>{
                        return <option value={c.name} key={c.id}>{c.name}</option>
                    })}
                   
                </select>
                <select onChange={e => {handleFilterCreate(e)}}>
                    <option value='ALL'>Todos</option>
                    <option value='created'>Creados</option>
                    <option value='api'>Existente</option>
                </select>                
                {currentDogs?.map((c) => {
                    return (
                        <fragment>
                            <Link to={"/home/" + c.id}>
                                <Card name={c.name} image={c.image} temperament={c.temperament} key={c.id} height_min={c.height_min} height_max={c.height_max} weight_min ={c.weight_min} weight_max ={c.weight_max} />
                            </Link>
                        </fragment>
                    )
                })

                }
                
                <Paginado dogPerPage={dogPerPage} allDogs={allDogs.length} paginado={paginado} />
            </div>
            <div>
                
            </div>
        </div>
    )
}

