import React from "react";

export const Card=({name, image, weight_min, weight_max, height_min, height_max, temperament})=>{
    return (
        <div>
            <h3>{name}</h3>
            <h4>{temperament}</h4>
            <h6>Peso minimo: {weight_min}</h6>            
            <h6>Peso maximo: {weight_max}</h6>            
            <h6>Altura minima: {height_min}</h6>
            <h6>Altura maxima: {height_max}</h6>
            <img src={image} alt="img not found" height='200px' weight= '250px' />
        </div>
    )
}

