import axios from 'axios';


//peticiones
export function getDogs(payload) {
    return async function(dispatch) {
      let json = await axios.get("http://localhost:3001/dogs")
       return dispatch ({
         type: "GET_DOG",
         payload : json.data
       })        
    };
  }

  
  export function searchDog (payload){
    return async function (dispatch){
      let json = await axios.get(`http://localhost:3001/dogs?name=${payload}`)?
      dispatch (requestDog([json.data])):
       dispatch(errorDog('No se encuentra su perro'))

      return dispatch ({
        type: "GET_SEARCH",
        payload: json.data
      })
    }
  }

  export function getTemp(payload){    
    return async function(dispatch){
      let jsons = await axios.get('http://localhost:3001/temperaments')
      return dispatch({
        type: 'GET_TEMP',
        payload: jsons.data
      })
    }
  }
 
  //filter

 export function filterCreated(payload){
   return {
     type: 'FILTER_CREATED',
     payload 
   }
 }


 export function filterTemp(payload){
   return{
     type:'FILTER_TEMP',
     payload
   }
 }


   //order 

 export function orderByName(payload){
   return{
     type:'ORDER_BY_NAME',
     payload
   }
 }
 
 export function orderByWeight(payload){
   return{
     type:'ORDER_BY_WEIGHT',
     payload
   }
 }

 //varios

 export const successDog= (dog) =>{
   return {
     type: 'SUCCESS_DOG',
     payload: dog
   }
 }
 
 export const errorDog = (error) => {
   return{
     type: 'ERROR_DOG',
     payload: error
   }
 }

 export const requestDog = ()=>{
   return{
     type: 'REQUEST_DOG'
   }
 }