import React from "react";

export  const Paginado = ({dogPerPage, allDogs, paginado})=>{
    const pageNumber = []

    for (let i = 0; i <= Math.ceil(allDogs/dogPerPage); i++) {
        pageNumber.push(i+1);               
    }
    pageNumber.pop() 
    return (
        <nav>
            <ul>
                {
                    pageNumber && pageNumber.map(n =>(
                        <li key={n}>
                        <button onClick={()=> paginado(n)}>{n}</button>
                        </li>
                    ))
                }
            </ul>
        </nav>
    )
}